@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@100;300;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  font-family: "Noto Sans JP", sans-serif;
}

body {
  width: 100vw;
  min-height: 100vh;
  overflow: hidden auto;
  background: #1a0841;
  color: #b9eaf5;
}

body #root {
  width: 100%;
  padding: 2rem;
  display: flex;
  flex-direction: column;
}

h1 {
  text-align: center;
}

body .main {
  width: 100%;
  padding: 4rem;
  display: flex;
  grid-gap: 2rem;
  gap: 2rem;
}

@media screen and (max-width: 768px) {
  body .main {
    flex-direction: column;
    padding: 0;
    padding: initial;
  }
}

.editor-container {
  width: 40%;
}
@media screen and (max-width: 768px) {
  .editor-container {
    width: 100%;
  }
}
.editor-container .editor {
  min-height: 80vh;
  width: 100%;
  padding: 2rem;
  background: #b9eaf5ee;
  color: #1a0841;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media screen and (max-width: 768px) {
  .editor-container .editor {
    min-height: 15rem;
    width: 100%;
    padding: 1rem;
    margin: auto;
  }
}

@media screen and (max-width: 768px) {
  h3 {
    text-align: center;
  }
  h3#heading-3- {
    text-align: left;
  }
}

.preview {
  display: flex;
  flex-direction: column;
  position: relative;
  padding: 1rem;
}
.preview pre {
  background: #3a3a3a;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  max-width: 100%;
  overflow-x: auto;
  padding: 1rem;
  align-self: center;
}
.preview pre code {
  padding: 0;
  max-width: 100%;
  word-wrap: wrap;
}
.preview code {
  font-family: monospace;
  background: #3a3a3a;
  padding: 0 4px;
}
.preview a {
  color: currentcolor;
}
.preview p:last-of-type {
  display: flex;
}
.preview p:last-of-type img {
  max-height: 10rem;
  object-fit: contain;
  align-self: center;
  margin: 0 auto;
}
@media screen and (max-width: 768px) {
  .preview {
    width: 100%;
  }
  .preview code {
    overflow: auto;
  }
}
